import { useEffect, useState } from 'react';
import Papa from 'papaparse'
import some from 'lodash/some'

export default (csv) => {
  const [data, setData] = useState()
  useEffect(() => {
    setTimeout(() => {
      Papa.parse(csv, {
        download: true,
        header: true,
        complete: (results) => {
          setData(results.data.filter(d => some(d, Boolean)));
        }
      });
    }, 500)
  }, [])
  return { data }
}
